:root{
    --hr-color: rgba(0, 0, 0, 0.09);
}

body{
    background-image: linear-gradient(to top, white 60%, rgb(246, 246, 246));
}

.blur-all{
    filter: blur(1.5px);
}

.promotions{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 20px;
}
.promotions::before{
    content: '';
    height: 6px;
    background-color: var(--hr-color);
    width: 200px;
}
.promotions h1{
    display: inline;
    margin: 25px 20px;
    background-image: linear-gradient(to top, red, rgb(152, 2, 2));
    padding: 20px;
    border-radius: 10px;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    color: white;
}
.promotions::after{
    content: '';
    height: 6px;
    background-color: var(--hr-color);
    width: 200px;
}

.images-container{
    display: flex;
    flex-flow: wrap;
    gap: 15px;
    justify-content: center;
}

.images-container img{
    box-shadow: 0 2px 10px 2px rgba(0, 0, 0, 0.297);
    border-radius: 8px;
    width: auto;
}

.images-container .aside-image-container{
    display: flex;
    flex-flow: column;
    gap: 15px;
}

