#ad3{
    content: url('https://cache.dominos.com/wam/prod/market/BR/_pt/images/promo/c10755b7-268f-46b0-94f0-dbff645e66e7.png');
}

@media(max-width: 1000px)
{
    .images-container img{
        width: 95vw;
    }
    #ad0{
        content: url(../../../public/navigation-images/large-ad1.png);
    }
    #ad1{
        content: url(../../../public/navigation-images/large-ad2.png);
    }
    #ad2{
        content: url(../../../public/navigation-images/large-ad2.png);
    }
}