.footer-container{
    background-color: rgb(0, 0, 0, .9);
    color: white;
    margin-top: 90px;
    padding: 25px 0;
}

.another-links{
    margin-bottom: 100px;
}
.another-links ul{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    list-style: none;
    gap: 95px;
    padding-inline-start: 0;
}
.another-links ul a{
    text-decoration: none;
    color: white;
}

.footer-container p{
    text-align: left;
    margin: 20px auto;
    font-size: small;
    padding: 0 50px;
}

@media(max-width: 1000px)
{
    .another-links ul{
        gap: 55px;
    }
}