body{
    text-align: center;
}

.container{
    z-index: 1;
    position: fixed;
    left: 50%;
    top: 15%;
    transform: translate(-50%,-15%);
    background-color: rgba(255, 255, 255);
    width: 500px;
    padding: 15px 15px;
    border: 1px solid black;
}

figure{
    margin: 0;
    text-align: end;
}

.close-icon > img{
    width: 30px;
}

.input-label{
    padding: 10px;
}