.cart-div{
    height: 100vh;
    width: 800px;
    background-color: rgba(0, 0, 0, 1);
    box-shadow: 0px 0px 25px 45px;
    position: fixed;
    right: 50%;
    top: 0;
    transform: translate(50%, 0);
    z-index: 1;
}

.close-menu{
    text-align: end;
}

.close-menu > img{
    filter: invert(100%);
    width: 60px;
    margin: 20px;
}

.cart-div p{
    word-wrap: break-word;
    color: white;
}

.list-of-pizzas p{
    margin: 50px 25px;
}

.cart-div > img{
    width: 200px;
}

@media(max-width: 800px){
    .cart-div{
        width: 100vw;
    }
}